/**editer les informations d'une machine */
import React, { Component } from 'react';
import ContentWrapper from '../Layout/ContentWrapper';
import {
    Card,
    CardBody,
    CardHeader,
    Row,
    Progress,
    Col,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    Form,
    CustomInput,
    NavItem,
    NavLink, } 
from 'reactstrap';
import 'react-datetime/css/react-datetime.css';
import classnames from 'classnames';
const stepNavitemStyle = {
    backgroundColor: 'bg-gray-dark'
};

/*color for next and previous button */
const previousColor = "inverse"
const nextColor = "success"

const containerStyle = {
    width: '100%',
    height: '400px'
};


class SignUpManager extends Component {
    constructor(props) { 
        super(props);        
    }
    
    toggleStep = this.props.toggleStep
    validateOnChange = this.props.validateOnChange
    hasError = this.props.hasError
    handleSubmit = this.props.handleSubmit
    formRef = this.props.formRef

    render() {  
        const username = this.props.manager.formComplete.username
        const email = this.props.manager.formComplete.email
        const role = this.props.manager.formComplete.role

        const nom = this.props.manager.formComplete.nom
        const prenom = this.props.manager.formComplete.prenom
        const nationalite = this.props.manager.formComplete.nationalite
        const adresse = this.props.manager.formComplete.adresse
        const telephone = this.props.manager.formComplete.telephone
        const cni = this.props.manager.formComplete.cni
        
        const terms = this.props.manager.formComplete.terms

        // tab item active
        const tab1 = this.props.manager.progressValue[0]
        const tab2 = this.props.manager.progressValue[1]
        const tab3 = this.props.manager.progressValue[2]
        
        return (            
            <ContentWrapper>
                {/* START form */}
                <div className="block-center col-md-8">
                    <Form
                    innerRef={this.formRef}
                    className="form-horizontal" 
                    name="formComplete" 
                    onSubmit={this.handleSubmit}
                    >
                        <Card className="card-default">
                            <CardHeader className="pb-0">
                                <Row>
                                    <div className="col pr-0"> <Progress color="success-light" value={tab1}/> </div>
                                    <div className="col p-0"> <Progress color="success-light" value={tab2}/> </div>
                                    <div className="col pl-0"> <Progress color="success-light" value={tab3}/> </div>
                                </Row>
                            </CardHeader>                            
                            <CardBody className="pt-0 pb-0">         
                                <Nav tabs justified >
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.manager.activeStep === '1'
                                        })}
                                        onClick={this.toggleStep('1')}
                                        >
                                         <h4 className="text-left my-3">
                                            1. Compte
                                         </h4>
                                        </NavLink>
                                    </NavItem>
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.manager.activeStep === '2'
                                        })}
                                        onClick={this.toggleStep('2')}
                                        >
                                        <h4 className="text-left my-3">
                                           2. Profil
                                        </h4>
                                        </NavLink>
                                    </NavItem>
                                    <NavItem style={stepNavitemStyle}>
                                        <NavLink
                                        tag="div"
                                        className={classnames({
                                            active: this.props.manager.activeStep === '3'
                                        })}
                                        onClick={this.toggleStep('3')}
                                        >
                                        <h4 className="text-left my-3">
                                          3. Terminer
                                         </h4>
                                        </NavLink>
                                    </NavItem>
                                </Nav>
                            </CardBody>    
                            <TabContent activeTab={this.props.manager.activeStep}>
                                {/*account */}                        
                                <TabPane id="tabPane1" tabId="1">
                                    <div className="pt-3 mb-3">
                                        {/*username */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Nom d'utilisateur</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="username"
                                                name="username" 
                                                placeholder="username" 
                                                invalid={this.hasError('formComplete','username','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={username}
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup>
                                         {/*email */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Adresse email</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="email"
                                                id="email"
                                                name="email" 
                                                invalid={this.hasError('formComplete','email','required')||this.hasError('formComplete','email','email')}
                                                onChange={this.validateOnChange}
                                                value={email}
                                                data-validate='["required", "email"]'
                                                readOnly
                                                />                                                                                         
                                            </div>
                                        </FormGroup>
                                         {/*role */}
                                         <FormGroup row>
                                            <label className="col-xl-2 col-form-label"> Rle </label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="role"
                                                name="role" 
                                                invalid={this.hasError('formComplete','role','required')}
                                                onChange={this.validateOnChange}
                                                value={role}
                                                data-validate='["required"]'
                                                readOnly
                                                />                                                                                        
                                            </div>
                                        </FormGroup>
                                    </div>   
                                    <hr/>
                                    <div className="d-flex">
                                        {/*<Button color="secondary">Previous</Button>*/}
                                        <Button
                                        className="ml-auto"
                                        color={nextColor}
                                        onClick={this.toggleStep('2')}
                                        >
                                            Suivant
                                        </Button>
                                    </div>
                                </TabPane>                               
                                {/*profil*/}
                                <TabPane id="tabPane2" tabId="2">
                                    <div className="pt-3 mb-3">
                                        {/*nom */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Nom</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="nom"
                                                name="nom" 
                                                placeholder="Nom" 
                                                invalid={this.hasError('formComplete','nom','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={nom}
                                                />
                                                <span className="invalid-feedback">Veuillez entrer votre nom complet</span>                                            
                                            </div>
                                        </FormGroup>
                                        {/*prenom */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Prnom</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="prenom"
                                                name="prenom" 
                                                placeholder="Entrer votre prenom ici" 
                                                invalid={this.hasError('formComplete','prenom','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={prenom}
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup>
                                        {/*telephone */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Numro de tlphone</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="telephone"
                                                name="telephone" 
                                                placeholder="telephone" 
                                                invalid={this.hasError('formComplete','telephone','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={telephone}
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup> 
                                        {/*cni */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Numro de CNI</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="cni"
                                                name="cni" 
                                                placeholder="cni" 
                                                invalid={this.hasError('formComplete','cni','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={cni}
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup>   
                                        {/*nationalite */}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Nationalite</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="nationalite"
                                                name="nationalite" 
                                                placeholder="nationalite" 
                                                invalid={this.hasError('formComplete','nationalite','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={nationalite}
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup> 
                                        {/*adresse*/}
                                        <FormGroup row>
                                            <label className="col-xl-2 col-form-label">Adresse</label>
                                            <div className="col-xl-10">
                                                <Input 
                                                type="text"
                                                id="adresse"
                                                name="adresse" 
                                                placeholder="adresse" 
                                                invalid={this.hasError('formComplete','adresse','required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                value={adresse}
                                                />
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>                                            
                                            </div>
                                        </FormGroup> 
                                    </div>
                                    <hr />
                                    <div className="d-flex">
                                        <Button outline color={previousColor} onClick={this.toggleStep('1')}>
                                            Precedent
                                        </Button>
                                        <Button
                                        className="ml-auto"
                                        color={nextColor}
                                        type="submit" 
                                        onClick={this.toggleStep('3')}
                                        >
                                            Suivant
                                        </Button>
                                    </div>   
                                </TabPane>
                                {/*terminer*/}
                                <TabPane id="tabPane3" tabId="3">   
                                    <div className="pt-3 mb-3">
                                        <FormGroup>
                                            <label className=" col-form-label text-info">Rgles et conditions d'utilisation</label>
                                            <div className="col-xl-10">
                                                En cliquant sur "Terminer", vous vous engagez  ne divulguer aucunes informations relatives aux outils, et aux donnes des utilisateurs Green Plan sous peine de poursuites judiciares.                            
                                            </div>
                                        </FormGroup>
                                        <fieldset>
                                            <CustomInput
                                                type="checkbox"
                                                id="terms"
                                                name="terms"
                                                label="J'accepte les conditions d'utilisations"
                                                invalid={this.hasError('formComplete', 'terms', 'required')}
                                                onChange={this.validateOnChange}
                                                data-validate='["required"]'
                                                checked={terms}
                                            >
                                                <span className="invalid-feedback">Veuillez remplir ce champ</span>
                                            </CustomInput>
                                        </fieldset>
                                    </div>
                                    <div className="d-flex">
                                        <Button outline color={previousColor} onClick={this.toggleStep('2')}>
                                            Precedent
                                        </Button>
                                        <Button
                                            type="submit" 
                                            className="ml-auto"
                                            color={nextColor}
                                        >                                              
                                            Terminer
                                        </Button>
                                    </div>
                                </TabPane>
                            </TabContent>                                            
                        </Card>
                        {/* END card */}
                    </Form>
                </div>
            </ContentWrapper>
            );
    }
 
}

export default SignUpManager;
