/**editer les informations d'une machine */
import React, { Component } from 'react';
import axios from 'axios';
import firebase from '../../../firebase';
import ContentWrapper from '../Layout/ContentWrapper';
import FormValidator from '../../Common/FormValidator';
import swal from 'sweetalert';
import HeadingMenu from '../HeadingMenu';
import SignUpManager from './SignUpManager';
import {url_managers} from '../../../urls'

const stepNavitemStyle = {
    backgroundColor: 'bg-gray-dark'
};


class CompleteManager extends Component {
    constructor(props) {
        super(props);        
        /*Ajouter les fonctionnalits par la suite, les horaires ou le planning */
        this.state = {
            uid: '',
            formComplete : {
                //account
                username : '',
                email: '',
                role: '',
                //profile
                nom: '',
                prenom: '',
                nationalite: '',
                adresse: '',
                telephone: '',
                cni: '',
                // terminer
                terms: false,
            },
            password: '',
            isLoading: true,
            activeStep: '1',
            progressValue: [100, 0, 0],
            path: [
                { 
                    label: 'Manager',
                    value: '/dashboard_manager',
                },
                { 
                    label: 'Completer vos informations',
                    value: '',
                }
            ]
        };
    }

    componentDidMount() {
        firebase.auth().onAuthStateChanged( (authUser) => {
            if(authUser) {
                console.log(authUser.uid)
                const uid = authUser.uid
                axios.get(`${url_managers}${uid}`)
                .then((res) => {
                    const manager = res.data.data
                    const email = manager.email
                    const role = manager.role.nom
                    this.setState({
                        formComplete : {
                            email: email,
                            role: role,
                        },
                        uid: uid, 
                    })
                })
                .catch((error) => {
                    console.log('Erreur', error)
                    swal('Erreur', 'Vous ne pouvez pas accder  cette page pour le moment, ressayer ultrieurement', 'error')
                })
            }
            else {
                this.props.history.push('/login')
            }
        })
    }
    
    toggleStep = activeStep => () => {
        // For submit we can obtain the form from the event
        // but for each step we need a global ref to the element
        const form = this.formCompleteRef;
        // To validate only the inputs in the current steps, we use an id to query the tabPane
        // and then find all form elements for the current step only.
        const tabPane = document.getElementById('tabPane' + this.state.activeStep);
        const inputs = [].slice.call(tabPane.querySelectorAll('input,select'));
        // Run validation of inputs
        const { errors, hasError } = FormValidator.bulkValidate(inputs);

        // Update state so the validation message are shown/hidden
        this.setState({
            [form.name]: {
                ...this.state[form.name],
                errors
            }
        });

        // and prevent change the if form is not valid
        if (!hasError) {
            const progessValue1 = [100, 0, 0];
            const progessValue2 = [0, 100, 0];
            const progessValue3 = [0, 0, 100];
            
            if (this.state.activeStep !== activeStep) {
                this.setState({
                    activeStep
                });
            }
            
            switch (activeStep) {
                case '1':
                    this.setState({
                        progressValue: progessValue1
                    });
                    break;
                case '2':
                    this.setState({
                        progressValue: progessValue2
                    });
                    break;
                case '3':
                    this.setState({
                        progressValue: progessValue3
                    });
                    break;
                default:
                    console.log('default')
            }
        }
    }

    /**
      * Validate input using onChange event
      * @param  {String} formName The name of the form in the state object
      * @return {Function} a function used for the event
    */
    validateOnChange = event => {
        const input = event.target;
        const form = input.form;
        const value = input.type === 'checkbox' ? input.checked : input.value;

        const result = FormValidator.validate(input);

        this.setState({
            [form.name]: {
                ...this.state[form.name],
                [input.name]: value,
                errors: {
                    ...this.state[form.name].errors,
                    [input.name]: result
                }
            }
        });
    };

    /* Simplify error check */
    hasError = (formName, inputName, method) => {
        return  this.state[formName] &&
                this.state[formName].errors &&
                this.state[formName].errors[inputName] &&
                this.state[formName].errors[inputName][method]
    }    

    handleSubmit = e => {
        const form = e.target;
        const inputs = [...form.elements].filter(i => ['INPUT', 'SELECT'].includes(i.nodeName))

        const { errors, hasError } = FormValidator.bulkValidate(inputs)

        this.setState({
            [form.name]: {
                ...this.state[form.name],
                errors
            }
        });

        console.log(hasError ? 'Form has errors. Check!' : 'Form Submitted!')
        e.preventDefault();
        console.log(hasError)
        if (!hasError) {
            this.complete();
        }    
    }

    complete = () => {
        const { username, nom, prenom, nationalite, adresse, telephone } = this.state.formComplete;
        const uid = this.state.uid
        console.log("dans complete")
        axios.put(`${url_managers}${uid}`, {
            username,
            nom,
            prenom,
            nationalite,
            adresse,
            telephone,
            cni,
        })
        .then((docRef) => {
            console.log("reinitialisation du formulaire")
            this.setState({
                formComplete : {
                    username: '',
                    password: '',            
                    nom: '',
                    prenom: '',
                    nationalite: '',
                    adresse: '',
                    telephone: '',
                    cni: '',
                    terms: false,
                },
            });
            //feedback
            console.log("Un manager vient de terminer son inscription ajout!");
            let message = "";
            swal("Felicitation!", `Bienvenue dans la Green Team, ${message}`, "success").then((value) => {
                this.props.history.push("/")
            }) 
        })
        .catch((error) => {
          console.error("Error adding document: ", error);
          swal("Desole!", "L'opration n'a pu tre effectue, veuillez recommencer ultrieurement", "warning")
        });
    }
    
    // Keep a reference to the form to access from the steps methods
    formRef = node => (this.formCompleteRef = node);    

    render() {
        return (            
            <ContentWrapper>
                <HeadingMenu path = {this.state.path}/>
                <SignUpManager 
                manager={this.state}
                formRef={this.formRef}
                toggleStep={this.toggleStep}
                validateOnChange = {this.validateOnChange}
                hasError={this.hasError}
                handleSubmit = {this.handleSubmit} />
            </ContentWrapper>
            );
    }

}

export default CompleteManager;
